const state = {
    products: [
        { id: 1, title: "One", desc: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam in aliquam turpis, eget feugiat purus. Vestibulum auctor, dui scelerisque placerat laoreet, enim dui faucibus sem, sed dignissim magna lorem at justo." },
        { id: 2, title: "Two", desc: "Integer magna nisi, feugiat id risus id, hendrerit fermentum dolor. Aliquam hendrerit sem non suscipit ornare. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Maecenas efficitur, felis at lacinia efficitur, metus est blandit ex, et porttitor leo nunc id magna." }
    ]
};

const getters = {
    allTodos: (state) => state.products
};

const actions = {
    getProducts({ commit }) {
        var products = [];
        var xmlhttp = new XMLHttpRequest();
        xmlhttp.onreadystatechange = function () {
            if (this.readyState == 4 && this.status == 200) {
                
                let xmlDoc = this.responseXML;
                let products = xmlDoc.getElementsByTagName("PRODUCT");

                for (let i = 0; i < products.length; i++) {
                    let title = products[i].getElementsByTagName("TITLE")[0];
                    let desc = products[i].getElementsByTagName("DESCRIPTION")[0];
                     let product = {
                         id:i,
                         title: title.childNodes[0].nodeValue,
                         desc: desc.childNodes[0].nodeValue
                     }
                     products.push(product)
                }
            }
        }
        xmlhttp.open("GET", "./products.xml", false);
        xmlhttp.send();

        commit('setProducts',products)

    }
};

const mutations = {
    setProducts: (state,data) => (state.products = data)
};

export default {
    state,
    getters,
    actions,
    mutations
}