<template>
  <div class="hello">
   
     <ProductBtn v-for="item in allTodos" :key="item.id" v-bind:product="item"
      v-bind:active="activeIndex" v-on:click.native="changeActiveTab(item.id)" />
     
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'
import ProductBtn from './ProductBtn.vue'

export default {
  name: 'Product',
  components:{
    ProductBtn
  },
  data() {
    return {
      activeIndex: 0
    }
  },
  computed: {
    ...mapGetters(['allTodos'])
  },
  methods: {
    changeActiveTab: function(id){
      if(id == this.activeIndex){
        this.activeIndex = 0
      }
      else{
        this.activeIndex = id
      }
     
    },
    ...mapActions(['getProducts'])
  },
  created() {
    // this.getProducts()
  },
}
</script>

